<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="" prop="keyword">
            <el-input v-model="queryForm.keyword" placeholder="" clearable></el-input>
          </el-form-item>
          <!-- <div class="tips">30</div> -->
          <el-form-item label="" prop="chooseDate">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="" start-placeholder="" end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <!-- <el-button @click.stop="add" icon="el-icon-plus"></el-button> -->
          <span>
             <i style="color: #f00">{{ queryForm.total }}</i>  <i style="color: #f00">{{ count }}</i> 
          </span>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <!-- <span @click.stop="exportData"></span> -->
            </div>
          </div>
        </div>

        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
          <el-table-column prop="rownum_" label="" width="100"> </el-table-column>
          <el-table-column prop="company" label=""> </el-table-column>
          <el-table-column prop="confirmNumber" label=""> </el-table-column>
          <el-table-column prop="contractName" label=""> </el-table-column>
          <el-table-column prop="contractMethod" label=""> </el-table-column>
          <el-table-column prop="submitDate" label=""> </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status === 1"></el-tag>
              <el-tag v-else type="danger"></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="check(scope.row, 'edit')"></el-link>
              <el-link :underline="false" type="primary" @click.stop="check(scope.row, 'search')"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->

      <el-dialog :title="btnType === 'edit' ? '' : ''" :visible.sync="dialogVisible" width="80%" :before-close="handleClose">
        <el-divider></el-divider>

        <!--  -->
        <table-data v-if="dialogVisible" :tableData="tableDetail" :userType="userType" :flag="flag" :btnType="btnType" @editTable="editTable" @report="report" @research="research"></table-data>
        <!--  -->

        <span slot="footer" class="dialog-footer">
          <!-- <el-button @click="dialogVisible = false"> </el-button> -->
          <el-button type="warning" @click="repulse"> </el-button>
          <el-button type="success" @click="submit"> </el-button>
          <el-button type="primary" @click="save"> </el-button>
        </span>
      </el-dialog>

      <!--  -->

      <el-dialog title="" :visible.sync="newConfirmDialog" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="company">
            <el-input v-model="ruleForm.company"></el-input>
          </el-form-item>
          <el-form-item label="" prop="contractName">
            <el-input v-model="ruleForm.contractName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="contractMethod">
            <el-input v-model="ruleForm.contractMethod"></el-input>
          </el-form-item>
          <el-form-item v-if="!isEdit" label="" prop="stdCodeList">
            <el-upload class="upload-demo" accept=".xlsx,.xls" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeExcelUpload">
              <i class="el-icon-upload"></i>
              <div class="el-upload__text"><i class="el-icon-upload2"></i> </div>
            </el-upload>
            <el-button type="primary" @click.stop="downModule" icon="el-icon-download"></el-button>
          </el-form-item>
          <el-table v-if="!isEdit" class="report_data" :data="ruleForm.stdCodeList" style="width: 100%">
            <el-table-column type="index" label="" width="50"> </el-table-column>
            <el-table-column prop="code" label="">
              <template slot-scope="scope"> {{ ruleForm.stdCodeList[scope.$index] }} </template>
            </el-table-column>
            <el-table-column label="" width="80">
              <template slot-scope="scope">
                <el-link :underline="false" type="danger" @click.stop="delDetail(scope.$index)"></el-link>
              </template>
            </el-table-column>
          </el-table>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="newConfirmSubmit('ruleForm')"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import tableData from '@/components/confirm/Confirm'
export default {
  components: { tableData },
  data() {
    var validateTabel = (rule, value, callback) => {
      // console.log(rule, value)
      if (!this.ruleForm.stdCodeList.length) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      queryForm: {
        keyword: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: '',
        roleCode: '10004'
      },
      count: '',
      tableData: [],
      tableDetail: [],
      dialogVisible: false,
      Info: {}, // 
      detailPort: false, // 

      newConfirmDialog: false,
      ruleForm: {
        id: '',
        company: '',
        contractName: '',
        contractMethod: '',
        stdCodeList: []
      },
      rules: {
        company: [{ required: true, message: '', trigger: 'blur' }],
        contractName: [{ required: true, message: '', trigger: 'blur' }],
        contractMethod: [{ required: true, message: '', trigger: 'blur' }],
        stdCodeList: [{ required: true, validator: validateTabel, trigger: 'blur' }]
      },
      userType: 3, // 1 21 32
      flag: 'create', // create-confirm-check-approve-
      btnType: 'edit', //  edit search
      isEdit: false
    }
  },

  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      // console.log(this.queryForm)
    },

    // 
    onSubmit() {
      // console.log(this.queryForm)
      this.loadData()
    },

    add() {
      this.newConfirmDialog = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm.stdCodeList = []
      })
    },

    // 
    check(info, type) {
      this.isEdit = true
      if (type === 'edit') {
        this.newConfirmDialog = true
        this.$nextTick(() => {
          this.$refs.ruleForm.resetFields()
          this.ruleForm = info
          this.ruleForm.stdCodeList = []
          this.ruleForm.id = info.id
        })
      } else {
        info.type = 'conf3'
        this.$router.push({ path: '/affirmDetail', name: 'AffirmDetail', params: { info: info } })
        this.$sessionData('set', 'affirmInfo', info)
      }
    },
    del(id) {
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/ack/rem/' + id, {}).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate

      console.log(params)
      this.httpGet('/standard/ack/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.count = res.ext1
        this.queryForm.total = res.total
      })
    },

    // 
    exportData() {
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      params.total = ''
      let paramArr = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/standard/ack/export?' + paramArr.join('&'))
    },
    // 
    report() {
      if (!this.detailPort) return this.$message.warning(''), false
      window.open(this.$baseRequestUrl + '/standard/ack/result/report/download/1?reportSequence=2')
      this.dialogVisible = false
    },

    //   ****************************************************************************************************************
    // 
    downModule() {
      window.open(this.$templateDownloadUrl + 2)
    },
    delDetail(index) {
      this.ruleForm.stdCodeList.splice(index, 1)
    },
    uploadAction() {
      // return this.$picUpload
      return this.$ExcelUpload
    },

    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.import(res.data.fileUrl)
    },

    // 
    import(fileUrl) {
      console.log(fileUrl)
      this.httpGet('/standard/ack/parseCode?fileUrl=' + fileUrl, '')
        .then(res => {
          this.ruleForm.stdCodeList = res
        })
        .catch(err => {})
    },
    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: ' 100MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 
    newConfirmSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          let params = Object.assign({}, this.ruleForm)
          // params.
          console.log(params)
          this.httpPost('/standard/ack/mod/' + this.ruleForm.id, params).then(res => {
            console.log(res)
            this.$message.warning('')
            this.loadData()
            this.newConfirmDialog = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    //  888***********************************************************************************************************

    loadDetail(id) {
      this.httpGet('/standard/ack/result/query/' + id, {}).then(res => {
        // console.log(res)
        this.detailPort = res.length ? true : false
        this.tableDetail = res
      })
    },
    // 
    save() {
      let compareVos = this.tableDetail
      this.httpPost('/standard/ack/result/save/' + this.info.id, compareVos)
        .then(res => {
          this.$message({ type: 'success', message: '!' })
          this.isEdit = false
        })
        .catch(err => {})
    },
    // 
    submit() {
      let compareVos = this.tableDetail
      this.httpPost('/standard/ack/result/confirm/' + this.info.id, compareVos).then(res => {
        // console.log(res)
      })
      // .catch(err => {})
    },
    // 
    repulse() {
      console.log(11)
    },

    // 
    editTable(e) {
      console.log(e)
      this.tableDetail = e
      this.isEdit = true
    },
    // 
    research() {
      this.loadDetail(this.info.id)
    },
    handleClose(done) {
      if (this.isEdit) {
        this.$confirm(' ')
          .then(_ => {})
          .catch(_ => {})
      } else {
        done()
      }
    }
  },

  mounted() {
    const now = new Date()
    const endDate = now
    const startDate = now - 8.64e7 * 30

    this.queryForm.endDate = this.$moment(endDate).format('YYYYMMDD')
    this.queryForm.startDate = this.$moment(startDate).format('YYYYMMDD')
    this.queryForm.chooseDate = [startDate, endDate]
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.tips {
  display: inline-block;
  line-height: 40px;
  color: #bbb;
  margin-right: 20px;
}
.el-range-editor {
  width: 300px;
}
/deep/#detail {
  .cell {
    text-align: center;
  }
}
.demo-ruleForm {
  width: 500px;
}
.report_data {
  padding-top: 20px;
  margin-left: 20px;
}
</style>
