<template>
  <div class="MT_table_cont item">
    <!--  -->
    <div class="btns" v-if="this.btnType === 'edit'">
      <el-button @click.stop="search" icon="el-icon-refresh-right"></el-button>
      <div class="btn_cont">
        <div class="choose_btn _color">
          <span @click.stop="exportData"></span>
        </div>
      </div>
    </div>
    <el-table border id="detail" :data="tableData" style="width: 100%">
      <el-table-column prop="rownum_" label="" width="60"> </el-table-column>
      <el-table-column prop="originalName" label="()" show-overflow-tooltip> </el-table-column>
      <el-table-column prop="originalNo" label="()()" width="230"> </el-table-column>
      <el-table-column prop="resultName" label="()" show-overflow-tooltip> </el-table-column>
      <el-table-column prop="resultNo" label="()()" width="230"> </el-table-column>
      <el-table-column prop="validateType" label="" width="100"> </el-table-column>
      <el-table-column prop="comments" label="" width="120" show-overflow-tooltip> </el-table-column>
      <!--  -->
      <el-table-column label="" width="80" v-if="this.btnType === 'edit'">
        <template slot-scope="scope">
          <el-link :underline="false" type="primary" @click.stop="edit(scope.row, scope.$index)"></el-link>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="" :visible.sync="dialogVisible" width="30%" append-to-body>
      <el-divider></el-divider>
      <!-- form -->
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="demo-ruleForm">
        <el-form-item label="()" prop="originalNo">
          <el-input v-model="ruleForm.originalNo" placeholder="()()"></el-input>
        </el-form-item>
        <!-- <el-form-item label="()" prop="resultNo">
          <el-input v-model="ruleForm.resultNo" placeholder="()()"></el-input>
        </el-form-item> -->
        <el-form-item label="" required>
          <el-select v-model="ruleForm.validateType" placeholder="">
            <el-option label="A" value="A"></el-option>
            <el-option label="B" value="B"></el-option>
            <el-option label="E" value="E"></el-option>
            <el-option label="F" value="F"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="comments">
          <el-input type="textarea" v-model="ruleForm.comments"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit('ruleForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['tableData', 'userType', 'flag', 'btnType'],
  data() {
    return {
      dialogVisible: false,
      ruleForm: {},

      rules: {
        originalNo: [{ required: true, message: '()()', trigger: 'blur' }],
        // resultNo: [{ required: true, message: '()()', trigger: 'blur' }]
      },
      index: 0
    }
  },
  methods: {
    // 
    search() {
      this.$emit('research')
    },
    exportData() {
      this.$emit('report')
    },
    edit(info, i) {
      this.index = i
      this.ruleForm = Object.assign({}, info)
      this.dialogVisible = true
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {

          let arr = Object.assign([], this.tabelData)
          arr[this.index] = this.ruleForm
          arr.map(item => {item.resultNoAndName = item.resultName ? item.resultName + item.originalNo : item.originalNo})
          this.$emit('editTable', arr)
          // console.log(this.ruleForm)
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },
  mounted() {
    console.log(this.userType) // 1 21
    console.log(this.flag) // create-confirm-check-approve-
    console.log(this.btnType) //  edit search
  }
}
</script>

<style lang="scss" scoped>
.item {
  border: none;
  padding: 0;
  margin-top: -10px;
}
.el-select {
  width: 100%;
}
</style>
