<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="" prop="code">
          <el-input v-model="ruleForm.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="csName">
          <el-input v-model="ruleForm.csName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="egName">
          <el-input v-model="ruleForm.egName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="year">
          <el-date-picker style="width: 300px" size="small" v-model="ruleForm.year" type="year" placeholder=""> </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="executeDate">
          <el-date-picker style="width: 300px" size="small" v-model="ruleForm.executeDate" type="date" placeholder=""> </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="expiryDate">
          <el-date-picker style="width: 300px" size="small" v-model="ruleForm.expiryDate" type="date" placeholder=""> </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="page">
          <el-input v-model.number="ruleForm.page" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="ruleForm.status" placeholder="">
            <el-option label="" value="A"></el-option>
            <el-option label="" value="W"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="ruleForm.type" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cnVal">
          <el-input disabled v-model="ruleForm.cnVal"></el-input>
          <el-link :underline="false" @click.stop="loadCascader(1)">{{ type == 1 ? '' : '' }}</el-link>
          <!-- <el-cascader v-model="ruleForm.csdc" :options="cnStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleCnChange"></el-cascader> -->
        </el-form-item>
        <el-form-item label="" prop="inVal">
          <el-input disabled v-model="ruleForm.inVal"></el-input>
          <el-link :underline="false" @click.stop="loadCascader(2)">{{ type == 1 ? '' : '' }}</el-link>
          <!-- <el-cascader v-model="ruleForm.inStClassifyAdm" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item label="" prop="necVal">
          <el-input disabled v-model="ruleForm.necVal"></el-input>
          <el-link :underline="false" @click.stop="loadCascader(3)">{{ type == 1 ? '' : '' }}</el-link>
          <!-- <el-cascader v-model="ruleForm.inStClassifyAdm" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item label="" prop="replaceSd">
          <el-input v-model="ruleForm.replaceSd" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="replacedSd">
          <el-input v-model="ruleForm.replacedSd" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sdRange">
          <el-input type="textarea" v-model="ruleForm.sdRange"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sign">
          <el-input v-model="ruleForm.sign" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>

      <!--  -->

      <el-dialog title="" :visible.sync="casLog" width="30%">
        <el-divider></el-divider>
        <el-cascader v-show="isCn === 1" :show-all-levels="false" @change="handleCnChange" v-model="ruleForm.csdc" ref="cascaderHandle" :props="cnprops" clearable></el-cascader>
        <el-cascader v-show="isCn === 2" :show-all-levels="false" @change="handleInChange" v-model="ruleForm.ics" ref="cascaderHandle" :props="inprops" clearable></el-cascader>
        <el-cascader v-show="isCn === 3" :show-all-levels="false" @change="handleNecChange" v-model="ruleForm.nec" ref="cascaderHandle" :props="necprops" clearable></el-cascader>

        <span slot="footer" class="dialog-footer">
          <el-button @click="casLog = false"> </el-button>
          <el-button type="primary" @click="casLog = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        csName: '',
        egName: '',
        type: '', //  ''   1 2 3 4
        standText: '', //  ''   0 1
        status: '', // ''   0 ''
        year: '',
        executeDate: '',
        expiryDate: '',
        csdc: '',
        ics: '',
        nec:'',
        replaceSd: '',
        replacedSd: '',
        sdRange: '',
        sign: '',
        cnVal: '',
        inVal: '',
        necVal:'',
      },
      // cnStAdmOptions: [], // 
      // inStAdmOptions: [], // 
      rules: {
        code: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ]
      },

      type: '',
      casLog: false,
      cnprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.cnProps
      },
      inprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.inProps
      },
      necprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.necProps
      },
      innodes: [],
      cnnodes: [],
      necnodes: [],
      isCn: 1 //  1cn  2in
    }
  },
  methods: {
    loadCascader(n) {
      this.casLog = true
      this.isCn = n
    },

    // 
    handleCnChange(value) {
      // console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.cnnodes.map(item => {
        if (item.value === id) {
          this.ruleForm.csdc = item.value
          this.ruleForm.cnVal = item.label
        }
      })
      this.hiddenDom(1)
    },

// 
    handleInChange(value) {
      // console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.innodes.map(item => {
        if (item.value === id) {
          this.ruleForm.ics = item.value
          this.ruleForm.inVal = item.label
        }
      })
      this.hiddenDom(1)
    },

    // 
    handleNecChange(value) {
      // console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.necnodes.map(item => {
        if (item.value === id) {
          this.ruleForm.nec = item.value
          this.ruleForm.necVal = item.label
        }
      })
      this.hiddenDom(1)
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.ruleForm)
          let params = Object.assign({}, this.ruleForm)
          params.executeDate = this.$moment(this.ruleForm.executeDate).format('YYYY-MM-DD')
          params.expiryDate = this.$moment(this.ruleForm.expiryDate).format('YYYY-MM-DD')
          params.year = this.$moment(this.ruleForm.year).format('YYYY')
          if (this.$route.params.type === 'edit') {
            this.httpPost('/standard/mod/' + params.id, params).then(res => {
              this.$message.success('')
              this.resetForm('ruleForm')
            })
          } else {
            this.httpPost('/standard/add', params).then(res => {
              this.$message.success('')
              this.resetForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    },

    loadData() {},
    // getReplace() {
    //   let params = {
    //     type: 1
    //   }
    //   this.httpGet('/standard/cate/query/', params).then(res => {
    //     this.cnStAdmOptions = res
    //   })
    //   params['type'] = 2
    //   this.httpGet('/standard/cate/query/', params).then(res => {
    //     this.inStAdmOptions = res
    //   })
    // },

    cnProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 1 }
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
          this.cnnodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    inProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 2 }
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
          this.innodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    necProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 3 }
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
          this.necnodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    hiddenDom(type) {
      setTimeout(() => {
        if (type === 1) {
          var el = document.getElementsByClassName('el-scrollbar')
          var doms = el[el.length - 1].getElementsByTagName('div')
          for (let i = 0; i < doms.length; i++) {
            const e = doms[i]
            if (e.innerHTML === '') {
              el[el.length - 1].style.display = 'none'
            }
          }
        } else {
          var el = document.getElementsByClassName('el-scrollbar')
          for (let i = 0; i < el.length; i++) {
            el[i].style.display = 'block'
          }
        }
      }, 0)
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.httpGet('/standard/detail/'+params.info.id, '' ).then(res => {
        this.ruleForm = res
        this.ruleForm. cnVal = res.csdcName
        this.ruleForm. inVal = res.icsName
        this.ruleForm. necVal = res.necName
        this.ruleForm.csdc = res.csdc
        this.ruleForm.ics = res.ics
        this.ruleForm.nec = res.nec
        this.ruleForm.type = this.ruleForm.type.toString()
      })
      console.log(this.ruleForm)
      this.type = 2
    } else {
      this.type = 1
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
</style>
