<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="" prop="status">
            <el-select v-model="queryForm.keywordType" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="keyword">
            <el-input v-model="queryForm.keyword" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <div class="btn_cont">
            <span>
               <i style="color: #f00">{{ queryForm.total }}</i>  <i style="color: #f00">{{ count }}</i> 
            </span>
            <div class="choose_btn _color">
              <span @click.stop="exportData"></span>
            </div>
          </div>
        </div>

        <!--  -->
        <el-table :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="loginName" label="/" width="150"> </el-table-column>
          <el-table-column prop="department" label=""> </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              {{ scope.row.userType === 0 ? 'KEY' : '' }}
            </template>
          </el-table-column>
          <el-table-column prop="focusNum" label="" width="120"> </el-table-column>
          <!--  -->
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row.userId)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="exportDetail(scope.row.userId)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <el-dialog title="" :visible.sync="dialogVisible" width="50%">
        <el-divider></el-divider>
        <el-table :data="tableDetail" style="width: 100%">
          <el-table-column prop="code" label="" width="180"> </el-table-column>
          <el-table-column prop="csName" label=""> </el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      trusteUserCount: 0,
      // trusteNum: 0,
      queryForm: {
        keyword: '',
        size: 10,
        page: 1,
        total: '',
        keywordType: '1'
      },
      count: '',
      queryFormDetail: {
        size: 10,
        page: 1,
        total: '',
        userId: ''
      },
      tableData: [],
      tableDetail: [],
      dialogVisible: false
    }
  },

  methods: {
    exportDetail(id) {
      window.open(this.$baseRequestUrl + '/standard/managed/detail/export?userId=' + id)
    },
    onSubmit() {
      this.loadData()
    },

    // 
    edit(id) {
      this.dialogVisible = true
      this.loadDetail(id)
    },

    loadData() {
      console.log('1')
      this.queryForm.total = ''
      this.httpGet('/standard/managed/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
        this.count = res.ext1
        this.focusNum = res.focusNum
      })
      // this.trusteNum = res.trusteNum
    },

    loadDetail(id) {
      this.queryFormDetail.total = ''
      this.queryFormDetail.userId = id
      this.httpGet('/standard/managed/detail', this.queryFormDetail).then(res => {
        this.tableDetail = res.rows
        this.queryFormDetail.total = res.total
      })
    },

    exportData() {
      window.open(this.$baseRequestUrl + '/standard/managed/export')
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
