<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove"></span>
            </div>
          </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="indexName" label="" width="100"> </el-table-column>
          <el-table-column prop="indexValue" label=""> </el-table-column>
          <!--  -->
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      queryForm: {
        size: 10,
        page: 1,
        total: '',
        standardId:''
      }
    }
  },

  methods: {

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    add() {
      this.$router.replace({ path: '/editTarget', name: 'EditTarget', params: { type: 'add',standardId: this.queryForm.standardId } })
    },

    // 
    edit(info) {
      this.$router.replace({ path: '/editTarget', name: 'EditTarget', params: { type: 'edit', info } })
    },

    del(id) {
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let indexIdList =[]
            indexIdList.push(id)
          this.httpPost('/standard/index/index/rem', indexIdList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let indexIdList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            indexIdList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/index/index/rem', indexIdList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/standard/index/index/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.queryForm.standardId = this.$route.params.info.id
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
