<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }">{{ parentMenu }}</el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="item_cont">
        <div style="margin-bottom: 20px ">
          <div class="cnname">{{ ruleForm.csName }}</div>
          <div class="egname">{{ ruleForm.egName }}</div>
          <el-tag v-if="ruleForm.type === 1" type="primary"></el-tag>
          <el-tag v-else-if="ruleForm.type === 2" type="primary"></el-tag>
          <el-tag v-else-if="ruleForm.type === 3" type="primary"></el-tag>
          <el-tag v-else-if="ruleForm.type === 4" type="primary"></el-tag>
          <el-tag v-else type="primary"></el-tag>
          <el-tag style="margin-left: 20px" v-if="ruleForm.status === 'A'" type="success"></el-tag>
          <el-tag style="margin-left: 20px" v-else type="danger"></el-tag>
        </div>
        <div v-if="ruleForm.type === 5" style="margin-bottom: 40px">
          <div class="cont_title"></div>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.name }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.code }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.certNo }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.publishDate }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.certOrg }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.zipCode }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.legalPerson }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.address }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">
                  <span v-if="ruleForm.teamInfo.patent"></span>
                  <span></span>
                </el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title"></el-col>
                <el-col :span="20">
                  <span v-if="status === 'A'"></span>
                  <span v-else></span>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title"></el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.teamInfo.serviceRange }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title"></el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.drafter }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title"></el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.draftingUnit }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title"></el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.mainContent }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
        </div>
        <div v-else style="margin-bottom: 40px">
          <div class="cont_title"></div>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title"></el-col>
                <el-col :span="19">{{ ruleForm.code }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title"></el-col>
                <el-col :span="19">{{ ruleForm.year }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title"></el-col>
                <el-col :span="19">{{ ruleForm.executeDate }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title"></el-col>
                <el-col :span="19">{{ ruleForm.status == 'A' ? '' : '' }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title"></el-col>
                <el-col :span="19">{{ ruleForm.expiryDate }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title"></el-col>
                <el-col :span="19">{{ ruleForm.page }}</el-col>
              </div>
            </el-col>
          </el-row>
        </div>
        <div style="margin-bottom: 40px">
          <div class="cont_title"></div>
          <el-row class="classify" :gutter="20">
            <el-col :span="2"></el-col>
            <el-col :span="20">{{ ruleForm.csdc }}</el-col>
          </el-row>
          <el-row class="classify" :gutter="20">
            <el-col :span="2"></el-col>
            <el-col :span="20">{{ ruleForm.ics }}</el-col>
          </el-row>
          <el-row v-if="ruleForm.type === 5" class="classify" :gutter="20">
            <el-col :span="2"></el-col>
            <el-col :span="20">{{ ruleForm.nec }}</el-col>
          </el-row>
        </div>
        <div style="margin-bottom: 40px">
          <div class="cont_title"></div>
          <div style="padding: 0 20px; line-height: 30px">{{ ruleForm.sdRange }}</div>
        </div>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        csName: '',
        egName: '',
        type: '', //  ''   1 2 3 4
        standText: '', //  ''   0 1
        status: '', // ''   0 ''
        year: '',
        executeDate: '',
        expiryDate: '',
        csdc: '',
        ics: '',
        replaceSd: '',
        replacedSd: '',
        sdRange: '',
        sign: '',
        cnVal: '',
        inVal: ''
      },
      parentMenu: ''
    }
  },
  methods: {
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    }
  },
  mounted() {
    const { params } = this.$route
    console.log(params)
    let id = this.$sessionData('get', 'stand_id')
    this.parentMenu = params.type == '2' ? '' : ''
    this.httpGet('/standard/detail/' + id, '').then(res => {
      this.ruleForm = res
      this.ruleForm.cnVal = res.csdc
      this.ruleForm.inVal = res.ics
    })
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .span {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
.el-form-item {
  margin-bottom: 0 !important;
}
/deep/.el-form-item__label {
  line-height: 36px;
}
/deep/.el-form-item__content {
  line-height: 36px;
}

.item_cont {
  padding: 20px 50px;
  .cnname,
  .egname {
    margin-bottom: 10px;
  }
  .el-col {
    line-height: 40px;
    .title {
      text-align: right;
    }
  }
  .cont_title {
    border-bottom: 1px solid #ddd;
    padding-left: 20px;
    border-left: 3px solid blue;
    padding: 10px 20px;
    font-weight: bold;
  }
  .classify {
    margin-left: 0 !important;
    margin-right: 0 !important;
    border-bottom: 1px solid #ddd;
  }
}
</style>
