<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="" prop="keyword">
            <el-input v-model="queryForm.keyword" placeholder="" clearable></el-input>
          </el-form-item>
          <!-- <div class="tips">30</div> -->
          <el-form-item label="" prop="chooseDate">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="" start-placeholder="" end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <!-- <el-button @click.stop="add" icon="el-icon-plus"></el-button> -->
            <span>  <i style="color: #f00">{{ queryForm.total }}</i>  <i style="color: #f00">{{ count }}</i>  </span>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="exportData"></span>
            </div>
          </div>
        </div>

        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
          <!--<el-table-column prop="rownum_" label="" width="100"> </el-table-column>-->
          <el-table-column prop="company" label=""> </el-table-column>
          <el-table-column prop="confirmNumber" label=""> </el-table-column>
          <el-table-column prop="contractName" label=""> </el-table-column>
          <el-table-column prop="contractMethod" label=""> </el-table-column>
          <el-table-column prop="submitDate" label=""> </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.flag === 'confirm'"></el-tag>
              <el-tag v-if="scope.row.flag === 'create'" type="danger"></el-tag>
              <el-tag v-if="scope.row.flag === 'check'" type="warning"></el-tag>
              <el-tag v-if="scope.row.flag === 'approve'" type="success"></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="80">
            <template slot-scope="scope">
              <el-link v-if="scope.row.status===1" :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <el-dialog title="" :visible.sync="dialogVisible" width="80%">
        <el-divider></el-divider>

        <!--  -->

        <div class="MT_table_cont item">
          <div class="btns">
            <!-- <el-button @click.stop="add" icon="el-icon-plus"></el-button> -->
            <div class="btn_cont">
              <div class="choose_btn _color">
                <span @click.stop="exportData"></span>
              </div>
            </div>
          </div>
        </div>
        <!--  -->
        <table-data></table-data>
        <!--  -->

        <!-- <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button> -->
        <!-- <el-button type="primary" @click="detailExport"> </el-button> -->
        <!-- </span> -->
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import tableData from '@/components/confirm/Confirm'
export default {
  components: { tableData },
  data() {
    return {
      queryForm: {
        keyword: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: '',
        roleCode: '10001'
      },
      count: '',
      tableData: [],
      tableDetail: [],
      dialogVisible: false,
      detailPort: false, // 

      flag: '' // create-confirm-check-approve-
    }
  },

  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      // console.log(this.queryForm)
    },
    onSubmit() {
      // console.log(this.queryForm)
      this.loadData()
    },

    // add() {
    //   this.$router.push({ path: '/editStandard', name: 'EditStandard', params: { type: 'add' } })
    // },

    // 
    edit(info) {
      // this.info = info
      // this.dialogVisible = true
      // this.loadDetail(info.id)
      info.type = 'check'
      this.$router.push({ path: '/affirmDetail', name: 'AffirmDetail', params: { info: info } })
      this.$sessionData('set', 'affirmInfo', info)
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate

      console.log(params)
      this.httpGet('/standard/ack/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.count = res.ext1
        this.queryForm.total = res.total
      })
    },

    // loadDetail(id) {
    //   this.httpGet('/standard/ack/result/query/' + id, {}).then(res => {
    //     console.log(res)
    //     this.detailPort = res.length ? true : false
    //     this.tableDetail = res
    //   })
    // },
    // 
    exportData() {
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      params.total = ''
      params.page = ''
      params.size = ''
      let paramArr = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/standard/ack/export?' + paramArr.join('&'))
    }
    // 
    // detailExport() {
    //   if (!this.detailPort) return this.$message.warning(''), false
    //   window.open(this.$baseRequestUrl + '/standard/ack/result/report/download/1?reportSequence=2')
    //   this.dialogVisible = false
    // }
  },

  mounted() {
    const now = new Date()
    const endDate = now
    const startDate = now - 8.64e7 * 30

    this.queryForm.endDate = this.$moment(endDate).format('YYYYMMDD')
    this.queryForm.startDate = this.$moment(startDate).format('YYYYMMDD')
    this.queryForm.chooseDate = [startDate, endDate]
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.tips {
  display: inline-block;
  line-height: 40px;
  color: #bbb;
  margin-right: 20px;
}
.el-range-editor {
  width: 300px;
}
/deep/#detail {
  .cell {
    text-align: center;
  }
}
.item {
  border: none;
  padding: 0;
  padding-bottom: 10px;
}
</style>
