<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="140px">
        <el-form-item label="" prop="keyWord">
          <el-input v-model="queryForm.keyWord" placeholder=" /  / "></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="queryForm.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="queryForm.type" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="5"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ppbj">
          <el-select v-model="queryForm.ppbj" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="queryForm.status" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value="A"></el-option>
            <el-option label="" value="W"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-show="isHandPick" label="" prop="startYear">
          <el-date-picker style="width: 300px" size="small" v-model="queryForm.startYear" @change="dateChange($event, 0)" type="year" placeholder=""> </el-date-picker>
        </el-form-item>
        <el-form-item v-show="isHandPick" label="" prop="startExecuteDate">
          <el-date-picker style="width: 300px" size="small" v-model="queryForm.startExecuteDate" @change="dateChange($event, 1)" type="date" placeholder=""> </el-date-picker>
        </el-form-item>
        <el-form-item v-show="isHandPick" label="" prop="csdc">
          <el-cascader :show-all-levels="false" @change="handleCnChange" v-model="queryForm.csdc" ref="cascaderHandle" :props="cnprops" clearable></el-cascader>
          <!-- <el-cascader v-model="queryForm.csdc" :options="cnStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleCnChange"></el-cascader> -->
        </el-form-item>
        <el-form-item v-show="isHandPick" label="" prop="ics">
          <el-cascader :show-all-levels="false" @change="handleInChange" v-model="queryForm.ics" ref="cascaderHandle" :props="inprops" clearable></el-cascader>
          <!-- <el-cascader v-model="queryForm.ics" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item v-show="isHandPick" label="" prop="nec">
          <el-cascader :show-all-levels="false" @change="handleNeChange" v-model="queryForm.nec" ref="cascaderHandle" :props="necprops" clearable></el-cascader>
          <!-- <el-cascader v-model="queryForm.ics" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item v-show="isHandPick" label="" prop="sign">
          <el-input v-model="queryForm.sign" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="select_btn">
          <div class="choose_condition" @click="chooseCondition">{{ isHandPick ? ' ' : ' ' }}</div>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="resetForm('queryForm')"></el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove"></span>
              <span @click.stop="push"></span>
              <span @click.stop="identify"></span>
              <span @click.stop="clearIdentify"></span>
            </div>
          </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="code" label="" width="220">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="detail(scope.row.id)">
                {{ scope.row.code }}
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="csName" label=""> </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              {{ scope.row.status === 'A' ? '' : '' }}
            </template>
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              {{ scope.row.ppbj === 0 ? '' : '' }}
            </template>
          </el-table-column>
          <el-table-column prop="year" label="" width="120"> </el-table-column>
          <el-table-column prop="executeDate" label="" width="180"> </el-table-column>
          <!--  -->
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <!--<el-link :underline="false" type="info" @click.stop="targetManege(scope.row)"></el-link>-->
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->
      <el-dialog title="" :visible.sync="pushDialogFormVisible" width="30%">
        <el-form :model="pushForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="pushForm.region" placeholder="">
              <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="pushDialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="pushStand"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="identifyDialogFormVisible" width="30%">
        <el-form :model="identifyForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="identifyForm.identifyName" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="identifyDialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="addSign"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        keyWord: '',
        code: '',
        type: '1',
        ppbj: '1',
        status: '',
        startYear: '',
        startExecuteDate: '',
        csdc: '',
        ics: '',
        nec: '',
        sign: '',

        size: 10,
        page: 1,
        total: ''
      },
      cnStAdmOptions: [], // 
      inStAdmOptions: [], // 
      isHandPick: false,

      tableData: [],
      multipleSelection: [],
      editInfo: {},

      formLabelWidth: '120px',
      pushForm: { region: '' }, // 
      identifyForm: { identifyName: '' }, // 
      platformList: [],
      pushDialogFormVisible: false,
      identifyDialogFormVisible: false,

      cnprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.cnProps
      },
      inprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.inProps
      },
      necprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.necProps
      },
      innodes: [],
      cnnodes: [],
      nenodes: []
    }
  },
  methods: {
    pushStand() {
      let standardIdList = []
      for (var i = 0; i < this.multipleSelection.length; i++) {
        standardIdList.push(this.multipleSelection[i].id)
      }
      let params = {
        standardIdList: standardIdList,
        platId: this.pushForm.region
      }

      this.httpPost('/push/standard/add', params).then(ret => {
        this.$message({ type: 'success', message: '!' })
        this.loadData()
        this.pushDialogFormVisible = false
      })
    },
    platform() {
      this.httpGet('/push/platform/queryAll', '').then(res => {
        this.platformList = res
      })
    },
    // 
    add() {
      this.$router.push({ path: '/editStandard', name: 'EditStandard', params: { type: 'add' } })
    },

    // 
    edit(info) {
      this.$router.push({ path: '/editStandard', name: 'EditStandard', params: { type: 'edit', info } })
    },

    //
    detail(id) {
      this.$router.push({ path: '/standardDetail', name: 'StandardDetail', params: { id } })
      this.$sessionData('set', 'stand_id', id)
    },

    // 
    targetManege(info) {
      this.$router.push({ path: '/targetManege', name: 'TargetManege', params: { info } })
    },

    del(id) {
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
          let standardVideoSort = [7, 6]
          // this.httpPost('/video/sort', standardVideoSort).then(ret => {})
        })
        .catch(() => {})
    },

    // 
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let idList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/rem/batch', idList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 
    push() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.pushDialogFormVisible = true
    },

    // 
    identify() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.identifyDialogFormVisible = true
    },
    addSign() {
      let standardIdList = []
      for (var i = 0; i < this.multipleSelection.length; i++) {
        standardIdList.push(this.multipleSelection[i].id)
      }
      let params = {
        standardIdList: standardIdList,
        sign: this.identifyForm.identifyName
      }
      this.httpPost('/standard/markSign', params).then(ret => {
        this.$message({ type: 'success', message: '!' })
        this.loadData()
        this.identifyDialogFormVisible = false
      })
    },

    // 
    clearIdentify() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let standardIdList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            standardIdList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/clearSign', standardIdList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 
    dateChange(e, flag) {
      if (flag === 1) {
        this.queryForm.startExecuteDate = this.$moment(e).format('YYYYMMDDhhmmss')
      } else {
        this.queryForm.startYear = this.$moment(e).format('YYYY')
      }
    },

    onSubmit() {
      console.log(this.queryForm)

      this.loadData()
    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    handleCnChange(value) {
      console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.cnnodes.map(item => {
        if (item.value === id) {
          console.log(item)
          this.queryForm.csdc = item.value
        }
      })
      this.hiddenDom(1)
    },

    handleInChange(value) {
      console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.innodes.map(item => {
        if (item.value === id) {
          this.queryForm.ics = item.value
        }
      })
      this.hiddenDom(1)
    },

    handleNeChange(value) {
      console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.nenodes.map(item => {
        if (item.value === id) {
          this.queryForm.nec = item.value
        }
      })
      this.hiddenDom(1)
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    // 
    chooseCondition() {
      this.isHandPick = !this.isHandPick
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/standard/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    },

    cnProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 1 }
      this.setVal(resolve, params, 'cnnodes')
    },
    inProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 2 }
      var nodes = []
      this.setVal(resolve, params, 'innodes')
    },
    necProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 3 }
      var nodes = []
      this.setVal(resolve, params, 'nenodes')
    },
    setVal(resolve, params, nodetype) {
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName, cateId: item.id })
          this[nodetype].push({ value: item.fullCode, label: item.cateName, cateId: item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    hiddenDom(type) {
      // type  1   2
      setTimeout(() => {
        if (type === 1) {
          var el = document.getElementsByClassName('el-scrollbar')
          var doms = el[el.length - 1].getElementsByTagName('div')
          for (let i = 0; i < doms.length; i++) {
            const e = doms[i]
            if (e.innerHTML === '') {
              el[el.length - 1].style.display = 'none'
            }
          }
        } else {
          var el = document.getElementsByClassName('el-scrollbar')
          for (let i = 0; i < el.length; i++) {
            el[i].style.display = 'block'
          }
        }
      }, 0)
    }
  },

  mounted() {
    this.loadData()
    this.platform()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
}
</style>
